import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col, Select } from "antd";
import {
  showroomsAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/Showrooms/showroomsSlice";
import { getMiniBanks } from "../../../../store/mini/miniSlice";

import NumericInput from "../../../components/data-entry/inputs/numberInput";
export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const AddShowrooms = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {
    props.getMiniBanks();
  }, []);

  function ChangeHandeler(e) {
    console.log("change Handeler ", e);
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      code: props.selectedData?.code,
      name: props.selectedData?.name,
      contact_number: props.selectedData?.contact_number,
      location: props.selectedData?.location,
      address: props.selectedData?.address,
      bank_id: props.selectedData.bank_id,
      accono: props.selectedData?.accono,
      ifsccode: props.selectedData?.ifsccode,
    };
    if (obj.name != null || obj.name == "") {
      props.showroomsAdd(obj);
    }
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const countryToUpdate = {
      id: props.selectedData.id,
      code: props.selectedData?.code,
      name: props.selectedData?.name,
      contact_number: props.selectedData?.contact_number,
      location: props.selectedData?.location,
      address: props.selectedData?.address,
      bank_id: props.selectedData.bank_id,
      accono: props.selectedData?.accono,
      ifsccode: props.selectedData?.ifsccode,
    };
    props.showroomsAdd(countryToUpdate);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.selectedData.id == null || props.selectedData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.selectedData.id == null || props.selectedData.id == 0
        ? "Add"
        : "Update"}{" "}
      Show Room
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      name: props.selectedData.name,
      contact_number: props.selectedData.contact_number,
      location: props.selectedData.location,
      bank_id: props.selectedData.bank_id,
      accono: props.selectedData.accono,
      ifsccode: props.selectedData.ifsccode,
      address: props.selectedData.address,
    });
  }, [props.selectedData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={616}
    >
      <Form
        layout="vertical"
        form={form}
        autoComplete="off"
        onFinish={() => {
          if (props.selectedData.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
      >
        <Row>
          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Show Room Name:"
              name="name"
              rules={[
                { required: true, message: "Please input your Show Room Name!" },
                {
                  pattern: new RegExp(
                    /^[a-zA-Z][a-zA-Z\s]+$/i
                  ),
                  message: "field does not accept numbers",
                }
              ]}
              hasFeedback
            >
              <Input
                id="Show Rooms"
                name="name"
                maxLength={50}
                style={{ width: "100%" }}
                placeholder="Show Room Name"
                value={props.selectedData?.name}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Contact Number :"
              name="contact_number"
              rules={[
                {
                  required: true,
                  message: "Please input your Contact Number!",
                },
              ]}
              getValueProps={() => { }}
              hasFeedback
            >
              <NumericInput
                id="contact_number"
                name="contact_number"
                style={{ width: "100%" }}
                placeholder="Contact Number"
                maxLength={10}
                value={
                  props.selectedData?.contact_number == null
                    ? ""
                    : props.selectedData?.contact_number
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Location:"
              name="location"
              rules={[
                { required: true, message: "Please input your Location!" },
              ]}
              hasFeedback
            >
              <Input
                id="Location"
                name="location"
                maxLength={50}
                style={{ width: "100%" }}
                placeholder="Location"
                value={props.selectedData?.location}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Bank :"
              name="bank_id"
              rules={[{ required: true, message: "Please select your Bank!" }]}
              hasFeedback>
              <Select
                showSearch
                placeholder="Select Bank"
                optionFilterProp="Bank"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "bank_id",
                    value: value,
                  });
                }}
                value={
                  props.selectedData.bank_id == null
                    ? ""
                    : props.selectedData?.bank_id
                }
              >
                {props.miniBanksList.map((bank, index) => {
                  return (
                    <Option key={index} value={bank.id}>
                      {bank.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Account Number:"
              name="accono"
              rules={[
                { required: true, message: "Please input your Account Number!" },
                {
                  min: 9,
                  message: "Account Number Must be minimum 9 characters"
                }
              ]}
              getValueProps={() => { }}
              hasFeedback>
              <NumericInput
                id="Account"
                name="accono"
                style={{ width: "100%" }}
                maxLength={18}
                placeholder="Account Number"
                value={
                  props.selectedData?.accono == null
                    ? ""
                    : props.selectedData?.accono
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="IFSC Code:"
              name="ifsccode"
              rules={[
                {
                  required: true,
                  message: "Please Enter Your IFSC Code!",
                },
                {
                  pattern: new RegExp(
                    /[A-Z|a-z]{4}[0][a-zA-Z0-9]{6}$/i
                  ),
                  message: "field does not accept IFSC code",
                },
              ]}
              hasFeedback
            >
              <Input
                id="ifsccode"
                name="ifsccode"
                maxLength={11}
                style={{ width: "100%" }}
                placeholder="IFSC Code"
                value={props.selectedData?.ifsccode}
                onChange={(e) => {
                  props.InputChangeValue({
                    key: e.target.name,
                    value: e.target.value.toUpperCase(),
                  });
                }}
              />
            </Form.Item>
          </Col>

          <Col md={24} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Address:"
              name="address"
              rules={[
                {
                  required: true,
                  message: "Please input your Address !",
                },
              ]}
              hasFeedback
            >
              <Input.TextArea
                id="Address "
                name="address"
                maxLength={150}
                style={{ width: "100%" }}
                placeholder="Address"
                value={props.selectedData?.address}
                onChange={ChangeHandeler}
              />
            </Form.Item>

          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ showrooms, mini }) {
  return {
    selectedData: showrooms.rowdata,
    model: showrooms.model,
    miniBanksList: mini.miniBanksList,
  };
}

export default connect(mapStateToProps, {
  showroomsAdd,
  isModelVisible,
  InputChangeValue,
  getMiniBanks,
})(AddShowrooms);
