import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";

import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import {
  getShowrooms,
  showroomsEdit,
  showroomsDelete,
  isModelVisible,
  SetShowroomsParams,
} from "../../../../store/Masters/Showrooms/showroomsSlice";
import AddShowrooms from "./addShowrooms​";

function Showrooms(props) {
  useEffect(() => {
    props.getShowrooms({ page: 1, search: "", page_size: 10 });
  }, []);
  const { Search } = Input;
  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 70,
      fixed: "left",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: 120,
      fixed: "left",
    },
    {
      title: "Show Room Name",
      dataIndex: "name",

      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "15%",
    },
    {
      title: "Contact Number",
      dataIndex: "contact_number",
      filterMode: "tree",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "15%",
    },
    {
      title: "Location",
      dataIndex: "location",
      filterMode: "tree",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "15%",
    },
    {
      title: "Address",
      dataIndex: "address",
      filterMode: "tree",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "15%",
    },
    {
      title: "Bank",
      dataIndex: "bankname",
      filterMode: "tree",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "15%",
    },
    {
      title: "Account Number",
      dataIndex: "accono",
      filterMode: "tree",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "15%",
    },
    {
      title: "IFSC Code",
      dataIndex: "ifsccode",
      filterMode: "tree",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "15%",
    },
    {
      title: "Actions",
      key: "action",
      width: 100,
      fixed: "right",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_showroom") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.showroomsEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Masters.delete_showroom") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.showroomsDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
        )}
        </Space>
      ),
    },
  ];

  const data = props.showroomslist.map((row, key) => ({
    ...row,
    index:
      (props.showroomsParams.page - 1) * props.showroomsParams.page_size +
      key +
      1,
    bankname: row.bank?.name,
  }));

  function onPageChange(page, pageSize) {
    props.SetShowroomsParams({
      ...props.showroomsParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getShowrooms({
      ...props.showroomsParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetShowroomsParams({ ...props.showroomsParams, search: val });
    props.getShowrooms({ ...props.showroomsParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Show Rooms Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_showroom") !== -1 &&
          "Add Show Room"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={14} className="hp-mb-10">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getShowrooms({
                      ...props.showroomsParams,
                      page: 1,
                      search: "",
                    });
                    props.SetShowroomsParams({
                      ...props.showroomsParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 1300 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.showroomsParams?.page}
                pageSize={props.showroomsParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                on
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddShowrooms />
    </Row>
  );
}

function mapStateToProps({ showrooms, system }) {
  return {
    showroomslist: showrooms.showroomslist,
    noofpages: showrooms.noofpages,
    listCount: showrooms.listCount,
    showroomsParams: showrooms.showroomsParams,
    loading: showrooms.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getShowrooms,
  showroomsEdit,
  showroomsDelete,
  isModelVisible,
  SetShowroomsParams,
})(Showrooms);
